	module.exports = {"data":{"post":{"id":"/Users/prasanna/projects/blog-gatsby/content/posts/2017-05-18--store_function_inside_redux_store/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"https://redux.js.org/\">Redux</a> is a predictable state container for Javascript. Redux state has to be serializable all the time.</p>\n<p>Object serialization is the process of converting an object‚Äôs state to a string from which it can later be restored.</p>\n<p>So, if you are trying to store a function inside the Redux state, you need to serialize them before persisting.</p>\n<blockquote>\n<p>Storing functions inside redux state is not a best practice in general. So try to avoid it.</p>\n</blockquote>\n<p>Javascript functions can be serialized quite easily, the challenge is in retrieving them from the store to execute.</p>\n<p>Below are the helper functions for persisting functions inside Redux state.</p>\n<p>  //Returns a string\nexport const serializeFunction = (func) => (func.toString());\n//serializeFunction(()=>console.log(‚ÄòHello!!‚Äò))\n// Output ==> ‚Äù()=>console.log(‚ÄòHello!!‚Äô)‚Äù</p>\n<p>The function to be stored in the state should be converted into string using serializeFunction.</p>\n<p>  //Returns a function\nexport const deserializeFunction = (funcString) => (new Function(`return ${funcString}`)());</p>\n<p>Convert the string from the redux store into a function using deserializeFunction</p>","htmlAst":{"type":"root","children":[{"type":"element","tagName":"p","properties":{},"children":[{"type":"element","tagName":"a","properties":{"href":"https://redux.js.org/"},"children":[{"type":"text","value":"Redux"}]},{"type":"text","value":" is a predictable state container for Javascript. Redux state has to be serializable all the time."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Object serialization is the process of converting an object‚Äôs state to a string from which it can later be restored."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"So, if you are trying to store a function inside the Redux state, you need to serialize them before persisting."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"blockquote","properties":{},"children":[{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Storing functions inside redux state is not a best practice in general. So try to avoid it."}]},{"type":"text","value":"\n"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Javascript functions can be serialized quite easily, the challenge is in retrieving them from the store to execute."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Below are the helper functions for persisting functions inside Redux state."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"  //Returns a string\nexport const serializeFunction = (func) => (func.toString());\n//serializeFunction(()=>console.log(‚ÄòHello!!‚Äò))\n// Output ==> ‚Äù()=>console.log(‚ÄòHello!!‚Äô)‚Äù"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"The function to be stored in the state should be converted into string using serializeFunction."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"  //Returns a function\nexport const deserializeFunction = (funcString) => (new Function(`return ${funcString}`)());"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Convert the string from the redux store into a function using deserializeFunction"}]}],"data":{"quirksMode":false}},"fields":{"slug":"/store_function_inside_redux_store/","prefix":"2017-05-18"},"frontmatter":{"title":"Storing a function in the Redux store","subTitle":"Redux state can be very useful to share data across the application. This post is about storing a function inside the redux store.","cover":null}},"author":{"id":"/Users/prasanna/projects/blog-gatsby/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Prasanna</strong> is a full stack web developer, loves to build user interfaces with Javascript. Considers himself as a geek, quick learner and a team player üòÉ </p>"},"footnote":{"id":"/Users/prasanna/projects/blog-gatsby/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>rants and writings through personal experience</li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"670156599751120"}}}},"pathContext":{"slug":"/store_function_inside_redux_store/"}}

